/* relevant styles */
.img_wrap {
  position: relative;
  height: 40vh;
  width: 20vw;
  margin: 1em;
  -webkit-box-shadow: 13px 9px 35px -7px rgba(0,0,0,0.64);
  -moz-box-shadow: 13px 9px 35px -7px rgba(0,0,0,0.64);
  box-shadow: 13px 9px 35px -7px rgba(0,0,0,0.64); 
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  border-radius: 30px;
}

.img_description {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(37, 39, 39,0.85);
  color: #fff;
  visibility: hidden;
  opacity: 0;
  margin: 0;
  /* transition effect. not necessary */
  transition: opacity .2s, visibility .2s;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  border-radius: 30px;
  padding: 1em;
  font-weight: bold;
}

.img_wrap:hover .img_description {
  visibility: visible;
  opacity: 1;
}

.projectsSec {
  background-color: transparent;
  padding: 10em 0;
  min-width:100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.img{
  height: 100%;
  width: 100%;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  border-radius: 30px;
}


@media only screen and (max-width: 768px) {
  .img_wrap{
    width: 100vw;
    height: 50vh;
  }

  .img_description{
    font-size: .8em;
  }
}

